import React, { useEffect, useState } from 'react';
import MainContext from './context/context';
import MainSection from './components/MainSection';
import Loader from './components/Loader';
import balloonCategories from './data/balloonCategories.json';
import parseOC from './external_data_fetching/parseOC';
import getContentful from './external_data_fetching/contentful';
import './style/main.scss';
import 'materialize-css';

function App({ dropdown, categories, BalloonStore }) {
  const [data, setData] = useState('empty');
  useEffect(() => {
    const getData = async () => {
      // setData(await parseOC());
      // setData(await getContentful());
      // setData(balloonsImages);
      // console.log(await parseOC());
      // console.log(await getContentful());
    };
    getData();
  }, []);
  if (categories) {
    return (
      <MainContext.Provider
        value={{
          categories,
          balloonCategories,
          dropdown,
        }}
      >
        <MainSection BalloonStore={BalloonStore} />
      </MainContext.Provider>
    );
  }
  return <Loader />;
}

export default App;
