import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import getContentful from './external_data_fetching/contentful';
import parseOC from './external_data_fetching/parseOC';
import { getCategories } from './external_data_fetching/opAPI';
import BallStore from './store/BalloonStore';

const containers = [...document.querySelectorAll('#root')];
getCategories().then((data) => {
  containers.forEach((item, index) => {
    ReactDOM.render(
      <App dropdown={`dropdown${index}`} BalloonStore={new BallStore()} categories={data} />,
      item,
    );
  });
});
