import React, { useState, useContext, useRef } from 'react';
import { MdClear } from 'react-icons/md';
import { FiShoppingCart } from 'react-icons/fi';
import html2canvas from 'html2canvas';
import styled from 'styled-components';
import { observer } from 'mobx-react-lite';
import { IoIosArrowDown } from 'react-icons/io';
import MainContext from '../../context/context';
import sendOrder from '../../external_data_fetching/sendOrderToOP';
// import BallStore from '../../store/BalloonStore';
import figureData from '../../data/figures.json';

const Controls = observer(({ BalloonStore }) => {
  const [startValue, setStartValue] = useState('Оберіть вид композиції');
  const [dropdownOpen, setDropOpen] = useState(false);
  const [showPop, setShowPop] = useState(false);
  const { dropdown } = useContext(MainContext);
  const select = useRef();
  const randomTarget = Math.floor(Math.random() * (999999 - 10000 + 1)) + 10000;
  const clearBalloons = () => {
    setStartValue('Оберіть вид композиції');
    BalloonStore.clearAll();
    select.current.value = 'default';
  };

  const setMax = (figure = '') => {
    if (figure.includes('fontaine')) return 10;
    if (figure.includes('cascade')) return 20;
    if (figure.includes('tree')) return 28;
    return 30;
  };
  const selectChangeHandler = (e) => {
    const itemClass = e.target.value;
    if (
      itemClass === 'decimal'
      || itemClass === 'walker'
      || itemClass === 'figure'
    ) {
      BalloonStore.createFigure(1, {
        id: figureData.find((item) => item.name === itemClass).id,
        type: itemClass,
      });
    } else if (itemClass === 'number') {
      BalloonStore.createFigure(2, {
        id: figureData.find((item) => item.name === itemClass).id,
        type: itemClass,
      });
    } else if (BalloonStore.balloons.length >= 3) {
      BalloonStore.createFigure(
        Math.min(
          Math.max(parseInt(BalloonStore.balloons.length, 10), 3),
          setMax(itemClass),
        ),
        {
          id: figureData.find((item) => item.name === itemClass).id,
          type: itemClass,
        },
      );
    } else {
      BalloonStore.createFigure(3, {
        id: figureData.find((item) => item.name === itemClass).id,
        type: itemClass,
      });
    }
    setStartValue('');
    BalloonStore.setFigureClass(itemClass);
  };

  const renderImage = async () => {
    try {
      document.querySelector(
        `#plane${+dropdown.replace(/^\D+/g, '')}`,
      ).style.boxShadow = 'none';
      const elem = document.getElementById(
        `plane${+dropdown.replace(/^\D+/g, '')}`,
      );
      const positionToScroll = window.scrollY;
      if (/(iPad|iPhone|iPod)/g.test(navigator.userAgent)) {
        window.scrollTo(0, 0);
      }

      await html2canvas(elem, {
        useCORS: true,
        logging: true,
        scrollY: (positionToScroll + 30) * -1,
        width: elem.offsetWidth,
        height: elem.offsetHeight,
        scrollX: 0,
      }).then(async (canvas) => {
        if (/(iPad|iPhone|iPod)/g.test(navigator.userAgent)) {
          window.scrollTo(0, positionToScroll);
        }
        const imgURL = canvas.toDataURL('image/jpeg');
        if (window.confirm('Завантажити зображення?')) {
          const a = document.createElement('a');
          a.href = imgURL;
          a.download = 'Композиція.jpg';
          a.click();
          a.remove();
        }
        await sendOrder(BalloonStore?.balloons, imgURL);
        clearBalloons();
      });
      document.querySelector(
        `#plane${+dropdown.replace(/^\D+/g, '')}`,
      ).style.boxShadow = '0 1px 4px rgba(0, 0, 0, 0.116), 0 1px 4px rgba(0, 0, 0, 0.144)';
      setShowPop(true);
      setTimeout(() => {
        setShowPop(false);
      }, 10000);
    } catch (e) {
      console.log({ e });
      alert('Нажаль виникла помилка');
    }
  };

  const orderHandler = () => {
    if (BalloonStore.allowOrder() === true) {
      renderImage();
    }
  };

  return (
    <div className="controls-panel">
      <Popup show={showPop} className={`popup popup${randomTarget}`}>
        <div className="popup-wrapper">
          <p>Ваше замовлення додане до корзини</p>
          <p>Перейдіть у Каталог та відкрийте кошик</p>
          <a
            onClick={() => setShowPop(false)}
            className="waves-effect waves-light btn flow-text"
          >
            Закрити
          </a>
        </div>
      </Popup>
      <div className="controls-panel-wrapper">
        <div className="control-panel-item control-select">
          <div className="balloon-select">
            <select
              ref={select}
              id={randomTarget}
              onBlur={() => setDropOpen(false)}
              onClick={() => setDropOpen(!dropdownOpen)}
              onChange={(e) => {
                selectChangeHandler(e);
              }}
            >
              <option value="default" selected disabled hidden>
                {startValue}
              </option>
              {figureData?.map((item, index) => (
                <option
                  key={index + item.label}
                  value={item.name}
                  onClick={() => setDropOpen(false)}
                >
                  {item.label}
                </option>
              ))}
            </select>
            <span className={`select-icon select-icon--${dropdownOpen ? 'up' : 'down'}`}><IoIosArrowDown /></span>
          </div>
        </div>
        <div className="control-panel-item control-range">
          <form action="#">
            <div className="range-field">
              <input
                type="range"
                className="slider"
                disabled={
                  startValue === 'Оберіть вид композиції'
                  || BalloonStore.figureClass.match(/figure|decimal|walker|number/g)
                }
                id="test5"
                value={BalloonStore.balloons.length}
                // figureClass === "cascade" ? 2 :
                step={1}
                min={BalloonStore.figureClass.includes('special') ? 1 : 3}
                max={setMax(BalloonStore.figureClass)}
                onChange={(e) => {
                  // setSValue(+e.target.value);
                  BalloonStore.createFigure(+e.target.value, BalloonStore.figureType);
                }}
              />
              <label htmlFor="test5">{BalloonStore.balloons.length}</label>
            </div>
          </form>
        </div>
        <div className="control-panel-item control-buttons">
          <button
            type="button"
            className="control-btn rnd-button btn__click"
            onClick={clearBalloons}
          >
            Очистити
            {' '}
            <span className="control-item-icon">
              <MdClear />
            </span>
          </button>
          <button
            type="button"
            className="control-btn rnd-button btn__click"
            id={`canv${+dropdown.replace(/^\D+/g, '')}`}
            onClick={orderHandler}
          >
            Замовити
            {' '}
            <span className="control-item-icon">
              <FiShoppingCart />
            </span>
          </button>
        </div>
        <div className="control-panel-item total">
          <p className="flow-text">
            Всього:
            {' '}
            {BalloonStore.totalPrice}
            {' '}
            грн.
          </p>
        </div>
      </div>
    </div>
  );
});
export default Controls;
const Popup = styled.div`
  top: ${(props) => (props.show === true ? '10px' : '-100%')};
`;
