import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import { RiCloseFill } from 'react-icons/ri';

import { observer } from 'mobx-react-lite';
// import BallStore from '../../store/BalloonStore';
import BalloonsLoader from '../BalloonsLoader';
import 'react-lazy-load-image-component/src/effects/blur.css';

const BalloonImgMenu = observer(
  ({
    display, setDisplay, BalloonStore,
  }) => {
    const container = useRef();
    const { index } = BalloonStore.selectedBalloon;
    const closeMenu = () => {
      setDisplay(false);
      document.querySelector('.plane').style.boxShadow = '0 6px 15px rgba(0, 0, 0, 0.068), 0 6px 15px rgba(0, 0, 0, 0.054)';
    };
    const handleClick = (e) => {
      if (container.current.contains(e.target)) {
        // inside click
        return;
      }
      // outside click
      closeMenu();
    };

    useEffect(() => {
      document.addEventListener('mousedown', handleClick);
      return () => {
        document.removeEventListener('mousedown', handleClick);
      };
    }, []);
    return (
      <BalloonsOptions
        ref={container}
        className="balloon-img-menu"
        displayMenu={display}
      >
        <div className="balloon-img-menu-wrapper">
          <div className="close-btn-container">
            <button
              type="button"
              className="close-btn btn__click"
              onClick={() => {
                closeMenu();
              }}
            >
              <RiCloseFill />
            </button>
          </div>

          {BalloonStore.balloonsLoading ? (
            <BalloonsLoader />
          ) : (
            <div className="menu-container">
              <div className="menu-item">
                <div className="menu-item-imgs">
                  {BalloonStore.categoryId
                    && BalloonStore.balloonsImages[BalloonStore.categoryId]?.map(
                      (product) => (
                        <button
                          type="button"
                          key={product.product_id}
                          className="img-container btn__click"
                          data-id={product.product_id}
                          onClick={() => {
                            BalloonStore.changeBalloonImg(
                              index,
                              product.product_id,
                              product.thumb,
                              parseInt(
                                product.price.replace(/₴|грн./g, ''),
                                10,
                              ),
                              BalloonStore.figureType.type,
                            );
                            setDisplay(false);
                          }}
                        >
                          <LazyLoadImage
                            alt="шар"
                            effect="blur"
                            src={product.thumb}
                            placeholderSrc="/loader.jpg"
                            placeholder={(
                              <p style={{ color: 'red!important', zIndex: 10 }}>
                                adasda
                              </p>
                            )}
                            wrapperClassName="lazy-img-preloader"
                          />
                          <PriceTag>
                            {parseInt(product.price.replace(/₴|грн./g, ''), 10)}
                            {' '}
                            грн
                          </PriceTag>
                        </button>
                      ),
                    )}
                </div>
              </div>
            </div>
          )}
        </div>
      </BalloonsOptions>
    );
  },
);

export default BalloonImgMenu;

// BalloonImgMenu.propTypes = {
//   balloonData: PropTypes.instanceOf(BalloonClass).isRequired,
//   display: PropTypes.bool.isRequired,
//   category: PropTypes.string.isRequired,
//   setDisplay: PropTypes.func.isRequired,
//   changeBalloonImg: PropTypes.func.isRequired,
// };
const PriceTag = styled.p`
  font-weight: bolder;
`;
const BalloonsOptions = styled.div`
  /* display: ${(props) => (props.displayMenu === false ? 'none' : 'flex')}; */
  /* transform:translate(${(props) => (props.displayMenu === false ? '-50%,100%' : '-50%,0%')}); */
  bottom: ${(props) => (props.displayMenu === false ? '-100%' : '0')};
`;
