/* eslint-disable no-tabs */
import { makeAutoObservable } from 'mobx';
import Balloon from '../components/Balloon';

class BalloonStore {
  count = 0;

  type = '';

  balloons = [];

  copiedBalloon = new Balloon(null, null, null, 0, null);

  selectedBalloon = null;

  totalPrice = 0;

  figureClass = '';

  categoryId = null;

  figureType = { id: null, type: '' };

  orderDisplay = false;

  balloonsImages = {};

  balloonsLoading = false;

  dropDownHeight = 0;

  clickedBalloon = null;

  constructor() {
    makeAutoObservable(this, {}, { deep: true });
  }

  fetchProducts() {
    if (!this.balloonsImages[this.categoryId]) {
      this.balloonsLoading = true;
      fetch(
        `${process.env.REACT_APP_API}api/product/bycategory&category=${this.categoryId}`,
      )
        .then((res) => res.json())
        .then((result) => {
          this.balloonsImages = {
            ...this.balloonsImages,
            [this.categoryId]: result.products,
          };
          this.balloonsLoading = false;
        })
        .catch((err) => console.log(err));
    }
  }

  setClickedBalloon({ balloonLeft, balloonTop, balloonParentHeight }) {
    this.clickedBalloon = { balloonLeft, balloonTop, balloonParentHeight };
  }

  setHeight(height) {
    this.dropDownHeight = height;
  }

  setFigureClass(classToSet) {
    this.figureClass = classToSet;
  }

  setcategoryId(id) {
    this.categoryId = id;
  }

  createFigure(count, { id = null, type = '' }) {
    let balloons;
    let legitCount;
    if (
      type === this.type
      || (type === 'bouquet'
        && (this.type === 'cascade' || this.type === 'fontaine'))
      || (type === 'fontaine'
        && (this.type === 'cascade' || this.type === 'bouquet'))
      || (type === 'cascade'
        && (this.type === 'bouquet' || this.type === 'fontaine'))
    ) {
      balloons = this.balloons;
      legitCount = this.count;
    } else {
      balloons = [];
      legitCount = 0;
    }
    if (count > legitCount) {
      for (let i = legitCount; i < count; i++) {
        balloons.push(new Balloon(type, null, i, 0, null));
      }
    } else if (count < legitCount) {
      balloons.splice(count, legitCount - count);
    }
    const totalPrice = balloons.reduce((acc, balloon) => acc + balloon.price, 0);
    this.type = type;
    this.figureType = { id, type };
    this.balloons = balloons;
    this.count = count;
    this.totalPrice = totalPrice;
  }

  selectBaloon(balloon) {
    this.selectedBalloon = new Balloon(
      balloon.type,
      balloon.img,
      balloon.index,
      balloon.price,
      balloon.id,
    );
  }

  clearAll() {
    this.createFigure(0, { id: null, type: '' });
    this.copiedBalloon = new Balloon(null, null, null, 0, null);
    this.selectedBaloon = null;
  }

  clearBalloonImg(index) {
    const shouldChange = this.balloons.find((item) => item.index === index);
    const itemIndex = this.balloons.indexOf(shouldChange);
    const { price } = shouldChange;
    this.balloons.splice(itemIndex, 1, new Balloon(
      this.balloons[index].type,
      null,
      index,
      null,
      null,
    ));
    // this.balloons[itemIndex] =
    this.totalPrice -= price;
  }

  changeBalloonImg(index, id, src, price, type) {
    const shouldChange = this.balloons.find((item) => item.index === index);
    const itemIndex = this.balloons.indexOf(shouldChange);
    let addPrice = 0;
    let minusPrice = 0;
    if (shouldChange && shouldChange.img !== src) {
      minusPrice = shouldChange.price;
      shouldChange.img = src;
      shouldChange.id = id;
      shouldChange.price = price;
      shouldChange.type = type;
      addPrice = price;
    }
    // this.balloons[itemIndex] = shouldChange;
    this.balloons.splice(itemIndex, 1, shouldChange);
    this.totalPrice = this.totalPrice - minusPrice + addPrice;
  }

  deleteBalloon(index) {
    const shouldDelete = this.balloons.find((item) => item.index === index);
    if (this.balloons.length > 3) {
      this.balloons = this.balloons.filter((item) => item.index !== index);
      this.totalPrice -= shouldDelete.price;
    }
  }

  copyBalloon(id, img, price, type) {
    this.copiedBalloon = new Balloon(type, img, null, price, id);
  }

  allowOrder() {
    const checkIfEmptyBalloon = this.balloons.filter((item) => !item.img);
    if (checkIfEmptyBalloon.length > 0 || this.balloons.length === 0) {
      alert('Заповніть всі кульки');
      return false;
    }
    return true;
  }
}

// export default new BalloonStore();
export default BalloonStore;
