/* eslint-disable prefer-const */
import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { observer } from 'mobx-react-lite';
import { ReactComponent as Round } from '../../data/samplesImgs/ball.svg';
import { ReactComponent as Number } from '../../data/samplesImgs/num.svg';
import { ReactComponent as Figure } from '../../data/samplesImgs/pony.svg';
import MainContext from '../../context/context';
import BalloonClass from '../Balloon';
// import BalloonStore from '../../store/BalloonStore';

const Balloon = observer(({ balloon, BalloonStore }) => {
  const { img, index, type = '' } = balloon;
  const { dropdown } = useContext(MainContext);
  let balloonType;
  if (type.includes('number') || type.includes('decimal')) {
    balloonType = <Number />;
  } else if (type.includes('figure') || type.includes('walker')) {
    balloonType = <Figure />;
  } else {
    balloonType = <Round />;
  }
  if (img !== null) {
    balloonType = (
      <img className="balloon-img" alt="ballon-img" src={img} height="160px" />
    );
  }

  const findAncestor = (el, cls) => {
    while ((el = el.parentElement) && !el.classList.contains(cls));
    return el;
  };
  const positionMenu = (el) => {
    let container = findAncestor(el, 'balloon');
    BalloonStore.setClickedBalloon({
      balloonLeft: container.offsetLeft,
      balloonTop: container.offsetTop,
      balloonParentHeight: container.offsetParent.offsetHeight,
    });
  };
  return (
    <>
      <button
        type="button"
        className="balloon dropdown-triggerr"
        data-target={dropdown}
        data-index={index}
        onClick={(e) => {
          positionMenu(e.target);
          BalloonStore.selectBaloon(balloon);
        }}
      >
        <div className="balloon-inner">
          {balloonType}
        </div>
      </button>
    </>
  );
});

export default Balloon;
Balloon.propTypes = {
  balloon: PropTypes.instanceOf(BalloonClass).isRequired,
  setBData: PropTypes.func.isRequired,
};
