/* eslint-disable jsx-a11y/no-noninteractive-element-interactions */
/* eslint-disable jsx-a11y/click-events-have-key-events */
import React, { useContext, useEffect, useRef } from 'react';
import { observer } from 'mobx-react-lite';
import MainContext from '../../context/context';
// import BallStore from '../../store/BalloonStore';

const BalloonContextMenu = observer(
  ({
    setDisplayImgMenu, BalloonStore,
  }) => {
    const { dropdown, categories } = useContext(MainContext);
    const {
      id, img, price, type, index,
    } = BalloonStore.selectedBalloon;
    const dropdownRef = useRef();
    const menuToRender = categories.filter((item) => item.figures.includes(BalloonStore.figureType?.id));
    const closeMenu = () => {
      dropdownRef.current.style.display = 'none';
    };
    const handleClick2 = (e) => {
      if (dropdownRef.current.contains(e.target)) {
        // inside click
        return;
      }
      // outside click
      closeMenu();
    };
    const positionMenu = () => {
      dropdownRef.current.style.display = 'block';
      const { balloonLeft, balloonTop, balloonParentHeight } = BalloonStore.clickedBalloon;
      const menuParent = document.querySelectorAll('.app-main-container')[+dropdown.replace(/^\D+/g, '')];
      let posTop = Math.abs(
        menuParent.offsetHeight - balloonParentHeight + balloonTop,
      );

      let posLR = balloonLeft;
      const differenceTop = menuParent.offsetHeight - (posTop + dropdownRef.current.offsetHeight);

      const differenceLR = menuParent.offsetWidth - posLR;
      if (differenceTop <= 0) {
        posTop -= Math.abs(differenceTop);
      }
      if (differenceLR < dropdownRef.current.offsetWidth) {
        posLR -= dropdownRef.current.offsetWidth - differenceLR;
      }
      dropdownRef.current.style.top = `${posTop}px`;
      dropdownRef.current.style.left = `${posLR}px`;
    };
    useEffect(() => {
      positionMenu();
    }, [BalloonStore.selectedBalloon]);
    useEffect(() => {
      document.addEventListener('mousedown', handleClick2);
      return () => {
        document.removeEventListener('mousedown', handleClick2);
      };
    }, []);

    return (
      <>
        <ul
          id={dropdown}
          ref={dropdownRef}
          className="ball-context-menu"
        >

          {BalloonStore.selectedBalloon?.img && (
            <>
              <li onClick={() => {
                BalloonStore.copyBalloon(id, img, price, type);
                closeMenu();
              }}
              >
                <p>Копіювати</p>
              </li>
              <li className="divider" tabIndex="-1"></li>
            </>
          )}
          {BalloonStore.copiedBalloon?.img && (
            <>
              <li
                onClick={() => {
                  BalloonStore.changeBalloonImg(
                    index,
                    BalloonStore.copiedBalloon.id,
                    BalloonStore.copiedBalloon.img,
                    BalloonStore.copiedBalloon.price,
                    BalloonStore.copiedBalloon.type,
                  );
                  closeMenu();
                }}
              >
                <p>Вставити</p>
              </li>
              <li className="divider" tabIndex="-1"></li>
            </>
          )}
          {BalloonStore.selectedBalloon?.img && (
            <>
              <li onClick={() => {
                BalloonStore.clearBalloonImg(index);
                closeMenu();
              }}
              >
                <p>Очистити вибір</p>
              </li>
              <li className="divider" tabIndex="-1"></li>
            </>
          )}
          <li onClick={() => {
            BalloonStore.deleteBalloon(index);
            closeMenu();
          }}
          >
            <p>Видалити шарик</p>
          </li>
          <li className="divider" tabIndex="-1"></li>
          {menuToRender.map((item) => (
            <>
              <li
                onClick={(e) => {
                  closeMenu();
                  setDisplayImgMenu(true);
                  BalloonStore.setcategoryId(item.id);
                  BalloonStore.fetchProducts();
                }}
                type={item.name}
              >
                <p>{item.name}</p>
              </li>
              <li className="divider" tabIndex="-1"></li>
            </>
          ))}
          <li
            onClick={() => {
              closeMenu();
            }}
          >
            <p>Закрити</p>
          </li>
        </ul>
      </>
    );
  },
);

export default BalloonContextMenu;

