import React, { useState, useContext, useEffect } from 'react';
import { observer } from 'mobx-react-lite';
import Balloon from '../drawPanelElements/Balloon';
import BalloonContextMenu from '../drawPanelElements/BalloonContextMenu';
import BalloonImgMenu from '../drawPanelElements/BalloonImgMenu';
import MainContext from '../../context/context';
// import BallStore from '../../store/BalloonStore';

const DrawComponent = observer(({ BalloonStore }) => {
  const [displayImgMenu, setDisplayImgMenu] = useState(false);
  const [planeHeight, setPlaneHeight] = useState(550);
  const { dropdown } = useContext(MainContext);
  const randomTarget = Math.random();
  const changeHeight = () => {
    if (
      BalloonStore.balloons?.length > 10
      && BalloonStore.figureType?.type !== 'tree'
    ) {
      const num = BalloonStore.balloons?.length - 10;
      // console.log({ num: BalloonStore.balloons?.length });
      console.log({ num: BalloonStore.balloons?.length, da: 'aga' });
      // if (num % 3 === 0) {
      console.log({ num: BalloonStore.balloons?.length, if: true });
      // console.log(500+num*50)
      setPlaneHeight(550 + num * 30);
      // }
      return;
    }
    setPlaneHeight(550);
  };

  useEffect(() => {
    changeHeight();
  }, [BalloonStore.balloons.length]);
  return (
    <>
      {BalloonStore.selectedBalloon && (
      <BalloonContextMenu
        BalloonStore={BalloonStore}
        setDisplayImgMenu={setDisplayImgMenu}
      />
      )}
      {BalloonStore.categoryId && (
        <BalloonImgMenu
          BalloonStore={BalloonStore}
          setDisplay={setDisplayImgMenu}
          display={displayImgMenu}
        />
      )}
      <div
        className="draw-component-wrapper"
        style={{ height: planeHeight }}
      >
        <div className="plane" id={`plane${+dropdown.replace(/^\D+/g, '')}`}>
          <div className={`balloons-container ${BalloonStore.figureType?.type}`}>
            <div className="tree-helper">
              {BalloonStore.balloons.map((item) => (
                <Balloon
                  BalloonStore={BalloonStore}
                  key={Math.random()}
                  randomTarget={randomTarget}
                  balloon={item}
                />
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
});

export default DrawComponent;
